.container {
  display: flex;
  justify-content: center;
  padding: 10px 5px;
}

.container span {
  cursor: pointer;
  width: 2%;
  text-align: center;  
  user-select: none;
  margin: 0 5px;
  border-radius: 80px;
  padding: 5px;
}

.container .active {
  background-color: #d2ff00;
  color: black;
  font-weight: 600;
}
 .inactive {
  background-color: #7d9900;
  color: #5f5f5f;
 }