.container {
  display: flex;
  align-items: flex-end;
}

.filter {
  margin: 15px 25px;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.filter label {
  margin: 0 5px;
  padding: 5px;
  cursor: pointer;
  background-color: #7d9900;
  color: #b5b5b5;
  width: 100%;
  text-align: center;
  transition: all 0.3s ease-in-out;
  box-shadow: 2px 2px 2px #000;
  border-radius: 5px;
}

.filter label:hover {
  background-color: #d2ff00;
  color: black;
  transition: all 0.3s ease-in-out;
}

.filter label input {
  visibility: hidden;
  display: none;
}